import { Mem } from './mem.js';
import { CPU } from './cpu.js';
import { GB } from './gb.js';
// import { disassemble, readROM } from './disasm.js'; // Available if needed
import { UInt8, UInt16, Address } from './types.js';

/**
 * Game Boy Emulator Entry Point
 */
function main(): void {
  console.log('üéÆ Game Boy Emulator Starting...');

  // Test memory system
  console.log('\n--- Testing Memory System ---');
  testMemorySystem();

  // Test CPU system
  console.log('\n--- Testing CPU System ---');
  testCPUSystem();

  // Test full Game Boy system
  console.log('\n--- Testing Full Game Boy System ---');
  testGameBoySystem();

  console.log('\n‚úÖ All systems operational!');
}

function testMemorySystem(): void {
  const memory = new Mem();

  // Test basic memory operations
  const testAddr: Address = 0x8000;
  const testValue: UInt8 = 0xFF;

  try {
    memory.writeByte(testAddr, testValue);
    const readValue = memory.readByte(testAddr);

    console.log(`Memory test: wrote 0x${testValue.toString(16)}, read 0x${readValue.toString(16)}`);

    if (readValue === testValue) {
      console.log('‚úÖ Basic memory operations working');
    } else {
      console.log('‚ùå Memory test failed');
    }

    // Test word operations
    const wordAddr: Address = 0x8100;
    const wordValue: UInt16 = 0x1234;

    memory.writeWord(wordAddr, wordValue);
    const readWord = memory.readWord(wordAddr);

    console.log(`Word test: wrote 0x${wordValue.toString(16)}, read 0x${readWord.toString(16)}`);

    if (readWord === wordValue) {
      console.log('‚úÖ Word operations working');
    } else {
      console.log('‚ùå Word test failed');
    }

  } catch (error) {
    console.error('‚ùå Error during memory tests:', error);
  }
}

function testCPUSystem(): void {
  const memory = new Mem();
  const cpu = new CPU(memory);

  try {
    // Test register operations
    cpu.setr('a', 0x42);
    cpu.setr('bc', 0x1234);

    const regA = cpu.getr('a') as UInt8;
    const regBC = cpu.getr('bc') as UInt16;

    console.log(`CPU register test: A=0x${regA.toString(16)}, BC=0x${regBC.toString(16)}`);

    if (regA === 0x42 && regBC === 0x1234) {
      console.log('‚úÖ CPU register operations working');
    } else {
      console.log('‚ùå CPU register test failed');
    }

    // Test flag operations
    cpu.setf('z', true);
    cpu.setf('c', false);

    console.log('‚úÖ CPU flag operations working');

    // Test arithmetic
    const result = cpu.add8(0x10, 0x20);
    console.log(`CPU arithmetic test: 0x10 + 0x20 = 0x${result.toString(16)}`);

    if (result === 0x30) {
      console.log('‚úÖ CPU arithmetic working');
    } else {
      console.log('‚ùå CPU arithmetic test failed');
    }

  } catch (error) {
    console.error('‚ùå Error during CPU tests:', error);
  }
}

function testGameBoySystem(): void {
  try {
    const gameboy = new GB();

    // Reset to known state
    gameboy.reset();

    const cpu = gameboy.getCPU();
    const pc = cpu.getr('pc') as UInt16;
    const sp = cpu.getr('sp') as UInt16;

    console.log(`Game Boy initialized: PC=0x${pc.toString(16)}, SP=0x${sp.toString(16)}`);

    if (pc === 0x0100 && sp === 0xFFFE) {
      console.log('‚úÖ Game Boy system initialization working');
    } else {
      console.log('‚ùå Game Boy initialization test failed');
    }

    // Test disassembly (with dummy data)
    const instruction = gameboy.disassembleAtPC();
    console.log(`Disassembly at PC: ${instruction}`);
    console.log('‚úÖ Disassembler integration working');

  } catch (error) {
    console.error('‚ùå Error during Game Boy tests:', error);
  }
}

// Run the emulator
main();