/**
 * Models the GameBoy as a whole from its constituent parts
 */

import { Mem } from './mem.js';
import { CPU } from './cpu.js';
import { disassembleInstruction } from './disasm.js';
import { Address, UInt16 } from './types.js';

export class GB {
  private mem: Mem;
  private cpu: CPU;

  constructor(rom?: Uint8Array) {
    this.mem = new Mem(rom);
    this.cpu = new CPU(this.mem);
  }

  // TODO: Add methods to run the emulator
  public run(): void {
    console.log('GameBoy emulator running...');
    // TODO: Implement main emulation loop
  }

  public step(): void {
    // Execute one CPU instruction
    const pc = this.cpu.getr('pc') as UInt16;
    // TODO: Fetch instruction, decode, execute
    console.log(`PC: 0x${pc.toString(16).padStart(4, '0')}`);
  }

  public disassembleAtPC(): string {
    const pc = this.cpu.getr('pc') as UInt16;
    const rom = new Uint8Array(0x8000); // Placeholder - should get from memory
    // TODO: Read from actual ROM data
    const result = disassembleInstruction(rom, pc as Address);
    return result.instruction;
  }

  public getMem(): Mem {
    return this.mem;
  }

  public getCPU(): CPU {
    return this.cpu;
  }

  public reset(): void {
    // Reset CPU to initial state
    this.cpu.setr('pc', 0x0100); // Game Boy starts at 0x0100
    this.cpu.setr('sp', 0xFFFE);
    this.cpu.setr('af', 0x01B0);
    this.cpu.setr('bc', 0x0013);
    this.cpu.setr('de', 0x00D8);
    this.cpu.setr('hl', 0x014D);
  }
}